body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  color: #212121;
  overflow-x: hidden;
  overflow-y: auto;
}

html, body, #root {
  height: 100%;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a.blue {
  color: #1976D2;
  text-decoration: none;
  font-weight: 600;
  text-shadow: 0px 0px 10px rgba(255,255,255,0.5);
}

a.blue:hover {
  text-decoration: underline;
}


