.nav__overlay {
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgb(0,0,0);
    max-width: 100vw;
    max-height: 100vh;
    z-index: 1; /* To draw it on top of body image overlay */
}


