.nav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2; /* To draw it on top of body image overlay */
}

.nav__menu {
    width: 25rem;
    background: #fff;
    color: rgba(0, 0, 0, 0.87);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    -webkit-transform: translateX(-12.5rem);
        -ms-transform: translateX(-12.5rem);
            transform: translateX(-12.5rem);
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    z-index: 1;
    font-size: 1rem;
    line-height: 1rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}

.nav__item {
    padding: 1rem;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.nav__item:hover {
    background: rgba(0,0,0,0.05);
}

.nav__item--selected {
    background: rgba(0,0,0,0.1);
}

.nav__item--complete,
.nav__item--complete .nav__item_date_time,
.nav__item--complete .nav__item_venue {
    color: rgba(0, 0, 0, 0.25);
}

.nav__item_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}

.nav__item_number {
    margin-right: 15px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    min-width: 24px;
}

.nav__item_details {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.nav__item_teams {
    font-weight: bold;
    margin-bottom: 5px;
}

.nav__item_venue {
    margin-bottom: 5px;
    color: rgba(0,0,0,0.5);
}

.nav__item_date_time {
    color: rgba(0,0,0,0.5);
}

.tabs .tab {
    text-transform: none;
}

.nav-wrapper {
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
.nav-wrapper, .nav__left {
    display: -ms-flexbox;
    display: flex;
}

.material-icons:hover {
    cursor: pointer;
}

.nav {
    width: 100%;
    position: relative;
}

.nav__bar {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}

.nav__title_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.nav__title {
    font-size: 22px;
    line-height: 22px;
    height: 100%;
    min-height: 60px;
    padding: 10px 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.5);
    -webkit-transform: translate(-30px);
        -ms-transform: translate(-30px);
            transform: translate(-30px);
}

.nav__title h1,
.nav__title h2 {
    margin: 0;
}

.nav__title h1 {
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 5px;
}

.nav__title h2 {
    font-size: 14px;
    line-height: 14px;
}

.nav__toggle {
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 60px;
    min-height: 60px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.5);
}

.nav__toggle_icon {
    padding: 0 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.nav__toggle_icon.material-icons {
    font-size: 36px;
    line-height: 36px;
}

@media screen and (max-width: 431px) {
    .nav__title {
        -webkit-transform: translate(0);
            -ms-transform: translate(0);
                transform: translate(0);
        width: 100%;
    }
    .nav__title_container {
        -ms-flex-pack: start;
            justify-content: flex-start;
    }
}

